@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@500&display=swap');
html{
  scroll-behavior: smooth;
}
#home-page{
  margin-top:0;
}
  .home{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    background-image: url("../../assets/images/bgimage.png");
    background-size: cover;
    background-position: center;
  }
  video{
    width:100%;
    height:100vh;
    object-fit: cover;
    align-items: center;
    justify-content: center;
    position:absolute;
    border: none;
  }
  .overlay{
    
    position:relative;
    z-index: 1;
    background:  linear-gradient(transparent,black);
    width:100vw;
    height:100vh;
    border: none;
  }
  .title{
    justify-content: center;
    position:absolute;
    flex-direction: column;
    z-index: 5;
    width:100vw;
    background-color: transparent;
    top:30vh;
    display: flex;
    align-items: center;
    justify-content: center;
    line-height: 1.2;
    

  }
  .wrap-title{
    display: flex;
    align-items: center;
    justify-content: center;
    width:75vw;
  }
  .title-image{
    width:200px;
    height:200px;
    border-radius: 20px;
    filter:drop-shadow(4px 8px 100px #000000);  
    
  }
  

 
  .title h1{
    text-shadow:  3px 4px 4px #000000;
    font-family: 'Raleway', sans-serif;
    font-size:64px;
    font-weight: 900;
    color:white;
    text-align: left;
    padding-left:2rem;
    text-shadow:  2px 2px 10px #000000;
  }
  @media only screen and (max-width:1250px){
    .title h1{
      font-size: 48px;
    }
  }
  @media only screen and (max-width: 1015px) {
    .wrap-title{
      display: block;
      justify-content: center;
      align-items: center;
      text-align:center;
    }
    video{
      height:100vh;
    }
    .title h1{
      font-size: min(10vw, 40px);
      text-align: center;
      padding: 0;
      -webkit-text-stroke: 0px #08707f;
      text-shadow:  2px 2px 4px #000000;
    }
    .title-image{
      width:min(30vw,128px);
      height:min(30vw,128px);
      border-radius: 20px;
      
    }
  }  
  .arrows {
    width: 60px;
    height: 72px;
    position:absolute;
    left: 50%;
    margin-left: -30px;
    bottom: 20px;
    z-index: 4;
  }
  
  .arrows path {
    stroke: #2994D1;
    fill: transparent;
   
    stroke-width: 1px;	
    animation: arrow 1s infinite;
    -webkit-animation: arrow 1s infinite; 
  }
  
  @keyframes arrow
  {
  0% {opacity:0}
  40% {opacity:1}
  80% {opacity:0}
  100% {opacity:0}
  }
  
  @-webkit-keyframes arrow
  {
  0% {opacity:0}
  40% {opacity:1}
  80% {opacity:0}
  100% {opacity:0}
  }
  
  .arrows path.a1 {
    animation-delay:-0.5s;
    -webkit-animation-delay:-0.5s;
  }
  
  .arrows path.a2 {
    animation-delay:-0.25s;
    -webkit-animation-delay:-0.25s; 
  }
  
  .arrows path.a3 {	
    animation-delay:0s;
    -webkit-animation-delay:0s; 
  }